<template>
<div>
	<div id="actionsBar">
		<div id="download_button" class="button cell"  v-on:click="download">
			Download
		</div>
		<div id="upload_button" class="button cell" v-on:click="clickInput">  
			Upload 
		</div>
		<input id="in" type="file" accept=".tnp" class="cell" style="display: none">
	</div>
</div>
</template>

<script>
import Vue from 'vue';

export default{
	name: "ActionsBar",
	components: {
	},

	data(){
		return {
			inputElement : null //populated in "mounted"
		}
	},
	
	methods: {
		download(){
			var state_str = JSON.stringify(this.$store.state);
			var fileBlob = new Blob([state_str], {type: "application/octet-binary"});
			var link = document.createElement("a");
			link.setAttribute("href", URL.createObjectURL(fileBlob));
			this.stripTrailingStarOffTitleIfExists();
			link.setAttribute("download", document.title+".tnp");
			link.click();
		},

		upload(){
			var store = this.$store;
			var tFile =  this.inputElement.files[0];
			tFile.text().then(res => {
				var new_state = JSON.parse(res); 	
				
				var shouldOverwriteFilename;
				if (store.state.notes[0].children.length == 0){
					shouldOverwriteFilename = true;
				}

				store.commit('loadState',{new_state});

				if(shouldOverwriteFilename){
					var newTitle = tFile.name.slice(0, -4) //Remove dot and extension
					setTimeout(function(){document.title=newTitle},50);
				}
				

			});
			this.inputElement.value = null; //Incase we want to reimport the same file
		},

		clickInput(){
			this.inputElement.click();
		},

		stripTrailingStarOffTitleIfExists(){
			if (document.title.endsWith('*')){
				document.title = document.title.slice(0,document.title.length-1)
			}
		},

		//Does not preform as expected; if removing a note creates a new orphan, this will not detect that.
		//Will probably not bother fixing, since that orphan will get taken care of during next save and space is not at a premium.
		returnCopyWithoutOrphans(notes){
			var deepCopy = JSON.parse(JSON.stringify(notes));
			var key;
			for (key in notes){
				if (key == 0){continue;}//Don't delete root
				if (deepCopy[key].parents.length == 0){ delete deepCopy[key]}
			}

			return deepCopy;
		}


	},

	created(){
		document.addEventListener('keydown', e => {
			switch(e.key){
				case 's':
					if (e.ctrlKey){
						e.preventDefault();
						this.download();
					}
					break;

				case 'o':
					if (e.ctrlKey){
						e.preventDefault();
						document.getElementById("in").click();
					}
					break;
			}
		});
	},

	mounted(){
		this.inputElement = document.getElementById("in");
		this.inputElement.addEventListener('input' ,e=>{
			this.upload();
		});
	}
};

	
</script>

<style scoped>
	#actionsBar {
		display: table;
		position: relative;
		width: 100%;
		height: 50px;
		border: 2px solid lightblue;
		background-color: red;
		z-index: 1;
	
	}


	.button {
		background-color: DodgerBlue;
		width: auto;
		cursor: pointer;
		margin-top: 10px;
		line-height: 100%;
		width: 10%;
		border: 3px solid green;

	}

	.cell {
		display: table-cell;
		vertical-align: middle;
		text-align: center;
		width: 20%;
		font-size: 20px;
	}

	#in {
		display: block;
		position: absolute;
		
		
	}

</style>