
export default {
	//In obj, replace keys and values equal to oldVal with newValing.
	//Replaces strings in arrays too, and if a value in obj is an object, call recursively.
	obj_val_replace: function(obj, oldVal, newVal){
		if ( (oldVal == newVal) || !obj ){return}

		var key;
		//Iterate over keys 
		for (key in obj){
			if (key == oldVal){
				obj[newVal] = obj[oldVal];
				delete obj[oldVal];
			}
		}

		for (key in obj){

			var val = obj[key];

			if (val == oldVal){obj[key]=newVal}

			if (typeof val == 'object'){

				if (val instanceof Array){
					obj[key] = val.map(x => (x==oldVal ? newVal : x));
				}

				else {
					this.obj_val_replace(val, oldVal, newVal);
				}
			}

		}
	},

	//Replace IDs of notes2 with IDs that won't conflict with notes1 
	shiftDuplicateIDs: function(notesToValidate,notesToUse){
		var key;
		var merged_notes = {};
		var id = 0; //variable to set next valid ID into

		for(key in notesToValidate){
			if (key in notesToUse){
				id = this.getNextValidID(notesToUse,notesToValidate,id);
				this.obj_val_replace(notesToValidate,key,id);
			}
		}
	},

	//Return first integer >= startFrom that is not a key in either notes1 or notes2
	getNextValidID(notes1,notes2,startFrom){
		var idToTry = startFrom;
		while((idToTry in notes1) || (idToTry in notes2)) idToTry++;
		return idToTry;
	},

	
}
