<template>
  <div id="AllContainer">
    <ActionsBar id="ActionsBar"/> 
    <NoteContainer/>
  </div>
</template>

<script>
  import ActionsBar from  './components/ActionsBar.vue'
  import NoteContainer from './components/NoteContainer.vue'

  export default{
    name: "App",
    components: {
      ActionsBar,
      NoteContainer
    },
    mounted(){
      window.onbeforeunload = function(){
        if (document.title.endsWith("*")){
          return "" //Prompts browser to prevent navigating away, and instead return message like "are you sure you want to leave?"
        }
      };
      
    }
  };

  
  
</script>

<style scoped>  

 #AllContainer{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
  } 

</style>