<template>
	<div id="root" :class="{shiftRight:parentIsNotRoot}">
		<div 
		id="topLine"
		ref="topLine"
		:class="{flex:true, border:true, selected:isSelected, focused:isFocused, hidden:isRoot}" 
		@click="focus"
	
		>
			<button
			@click="toggleButton"
			:class="{boldText:hasChildren}"
			> 
				{{button_text}} 
			</button>
			
			<textarea 
			ref="textarea"
			type="text" 
			v-show="isEditable"
			class="contentBox"
			v-model:value="markup"
			>	
			</textarea>

			<span 
			id="contentSpan"
			ref="contentSpan"
			v-show="!isEditable"
			v-html="markup"
			class="contentBox">
			</span>
		</div>
		
		<keep-alive>
			<div v-if="isExpanded" id="childrenDiv" ref="childrenDiv">
				<NoteInstance  v-for="child in children" :noteid="child" :key="child"/>
			</div>
		</keep-alive>

		<vue-context ref="contextmenu">
		    <li>
		        <a href="#" @click.prevent="ctxmenuOptionOnClick($event.target.innerText)">Option 1</a>
		    </li>
		    <li>
		        <a href="#" @click.prevent="ctxmenuOptionOnClick($event.target.innerText)">Option 2</a>
		    </li>
		</vue-context>

	</div>
</template>


<script>
import {mapState, mapGetters} from 'vuex';
import { VueContext } from 'vue-context';

export default {

	name: 'NoteInstance',
	
	components: {
		VueContext
	},

	props:{
		noteid: Number
	},

	data(){
		
		return {
			
			noteContainer: this.$parent.noteContainer,
			downArrow: '˅',
			rightArrow: '>',
			button_text: '>',
			isEditable: false,
		}
	},

	methods: {
		ctxmenuOptionOnClick(innerText){
			console.log(`Innertext is ${innerText}`);
		},

		resize(){
			var tls = this.$refs.topLine.style;
			var contentBox = this.isEditable ? this.$refs.textarea : this.$refs.contentSpan
			
			tls.height = 0;
			tls.height = contentBox.scrollHeight + "px";
		},

		focus(event){
			var appendMode = event ? event.ctrlKey : false;
			this.noteContainer.focus(this, appendMode);
		},
		
		addNote(note){
			this.$store.commit("addChildToParent",{child: note, parent: this.note});
			this.expand();
		},

		//getPreviousVisibleInstance, getNextVisibleInstance, and getLastChild are getters because if they
		//are properties the property doesn't update on some shift+up/down-s 
		getPreviousVisibleInstance(){
			
			if (this.$parent == this.noteContainer && this.indexAsSibling == 0){return this}
				

			var lastvi = this.getPreviousSibling();
			if (lastvi) { 
				while(lastvi.isExpanded && lastvi.hasChildren){
					lastvi = lastvi.getLastChild();
				}
				return lastvi;
			}
			else{
				return this.$parent
			}
		},

		getNextVisibleInstance(){
			
			//If this is expanded and has children, return the first one
			if(this.isExpanded && this.hasChildren){
				var firstChildNoteID = this.note.children[0];
				var firstChildNote = this.$store.state.notes[firstChildNoteID];
				var firstChildInstance = this.$children.filter(child => child.note==firstChildNote)[0];
				return firstChildInstance;
			}

			var recFn = function(instance){
				if (instance.isRoot){return instance}
				var nextSib = instance.getNextSibling(); 
				if (nextSib != null){return nextSib}
				return recFn(instance.$parent);
				
			}

			var ret=recFn(this);
			return ret.isRoot ? this : ret;
		},

		//Return previous instance, where order is defined by the note. If there is none, return null.
		getPreviousSibling(){
			var siblings = this.$parent.note.children; 
			var index = siblings.indexOf(this.note.id);
			if (index<1){return null}
			var prevSibNoteIndx = siblings[index-1];
			var prevSibNote = this.$store.state.notes[prevSibNoteIndx];
			var prevSibInstance = this.$parent.getChildInstances().filter(inst => inst.note==prevSibNote)[0];
			return prevSibInstance;
		},

		//Symmetric to above
		getNextSibling(){
			
			var siblings = this.$parent.note.children; 
			var index = siblings.indexOf(this.note.id);
			if (index == siblings.length-1){return null} 
			var nextSibNoteIndx=siblings[index+1];
			var nextSibNote = this.$store.state.notes[nextSibNoteIndx];
			var nextSibInstance = this.$parent.getChildInstances().filter(inst => inst.note==nextSibNote)[0];
			return nextSibInstance;
		},
			

		getLastChild(){return this.childInstances[this.childInstances.length-1]},

		toggleButton(){this.button_text = this.button_text == this.rightArrow ? this.downArrow : this.rightArrow},
		expand(){this.button_text = this.downArrow},
		collapse(){this.button_text = this.rightArrow},
		removeFromParent(){this.$store.commit("removeChildFromParent",{child: this.note, parent: this.$parent.note})},
		getChildInstances(){
			var children = this.$children
			var ret=children.filter(comp => comp.note!=undefined);
			return ret;
		},
		getChildInstance(note){
			return this.$children.filter(inst=>inst.note==note)[0];
		},
		getIndexAsSibling(){
			if(this.isRoot){return 0}
			return this.$parent.childInstances.indexOf(this)
		},
		//tag=b|i|u . Looks at previous text and adds an opening or closing tag as appropriate.
		addTag(tag){
				//Add a closing tag iff there is a last tag that was the opening tag
				var addClosingTag = function(tag){
					var regex;
					switch(tag){
						case 'b':
							regex = /<\/?b>/g;
							break;
						case 'u':
							regex = /<\/?u>/g;
							break;
						case 'i':
							regex = /<\/?i>/g;
							break;
					}
					var matches = this.markup.match(regex);

					if (matches == undefined){return false}

					var last_match = matches[matches.length-1];
					if (last_match == "</"+tag+">"){return false}
					
					return true;

				}.bind(this);
				
				var strToAdd = "<" + (addClosingTag(tag) ? "/" : "") +  tag + ">";
				this.markup += strToAdd;
		}
	},
 
	computed: {
		...mapState({
			children(state){
				return state.notes[this.noteid].children;
			},

			note(state){
				return state.notes[this.noteid];
			},

			parentNote(state){
				if (this.isRoot){ return undefined}
				return state.notes[this.$parent.note.id];
			}

		}),

		markup: {
			set(markup){
				this.$store.commit('setMarkup',{note:this.note, markup});
				this.resize();

			},
			get(){return this.note.markup}
		},

		//Array of instances from (noteContainer,this]
		path(){
			if (this.$parent == this.noteContainer){return [this]}
			return [...this.$parent.path,this]
		},

		isVisible(){
			if (this.$parent == this.noteContainer){return true}
			if (this.$parent.isVisible && this.$parent.isExpanded){return true}
			return false
		},

		childInstances(){return this.getChildInstances()},

		isSelected(){ return this.noteContainer.selectedInstances.includes(this)},
		isFocused(){ return this.noteContainer.focusedInstance == this},
		isExpanded(){ return this.button_text == this.downArrow},
		indexAsSibling(){return this.getIndexAsSibling()},
		textarea(){return this.$refs.textarea},
		hasChildren(){
			return this.note.children.length>0;
		},
		isRoot(){return this.noteid==0},
		parentIsNotRoot(){return !this.$parent.isRoot},
	},

	watch: {
		isEditable : function(){
			this.$nextTick(function(){
				this.resize();
				if(this.textarea){this.textarea.focus()}
			});
		},

		markup : function(){
			if (!document.title.endsWith('*')){document.title += '*'}
			window.tnk=this.textarea;
		},

		children : function(){
			if (!document.title.endsWith('*')){document.title += '*'}
		}
	},

	mounted(){
		this.resize();
		
		//Autoexpand root and autofocus it's first child 
		if (this.isRoot){
			this.focus();
			this.button_text = this.downArrow;
		}
	}

};

</script>

<style lang="scss">
	@import '~vue-context/src/sass/vue-context';
</style>

<style scoped>

	button {
			padding: 0;
			border: 0;
			padding: 3px;
			border-right: 1px solid black;
			outline: none;
		}

	textarea {
		width: 100%;
		overflow-y: hidden;
		outline: none;
		border-left: none;
	}

	#contentSpan {
		padding-left: 2px;
		min-height: 17px;
	}

	#childrenDiv{
		display: flow-root;
	}


	.shiftRight{
		margin-top: 1px;
		float: right;
		width: 99%;

	}

	#topLine{
		height: auto;
		overflow-y: hidden;
	}

	.flex {
		display: flex;
		flex-basis: min-content;
	}

	.contentBox {
		background: white;
		width: 100%;
	}


	.border {
		border: 1px solid black;
	}


	.selected { 
	    border-color: #4d94ff;
	    border-width: 2px;
	}

	.focused { 
	    border-color: red;
	    border-width: 2px;
	}

	.boldText{
		font-weight: bold
	}

	.hidden {
		display: none;
	}

</style>